/* Universal */

body {
    background-color: #ccc;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    font-family: Arial, sans-serif;
    position: relative;
    padding: 0;
    margin: 0;
    z-index: 1;
    font-size: 16px;
}

#root {
    width: 80%;
}

.page-container {
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.table-wrap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
}

.btn {
    border: 2px solid #ffa500;
    padding: 10px;
    border-radius: 5px;
    height: 30px;
    background-color: #000;
    color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 40px;
    font-size: 20px;
    outline: none;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    font-weight: bold;
}

.btn:hover {
    cursor: pointer;
    background-color: #fff;
    color: #000;
}

.btn:active {
    opacity: 0.5;
}

.btn.small,
.btn.large {
    margin: 20px;
    font-size: 16px;
}

/* Modal */

.modal {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    left: 0;
    z-index: 2;
    height: 100vh;
    width: 100vw;
    background-color: #fff;
    opacity: 0.8;
}

.modal__window {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    color: #000;
}

.modal__window > p {
    font-size: 2em;
    margin: 10px 0;
}

.figure {
    background-image: url('data/oval.svg');
    background-repeat: no-repeat;
    background-position: center;
    color: #000;
    height: 50px;
    width: 50px;
}

/* Search */

.search {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
    padding: 20px 0 20px 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
}

.search__txt {
    border: 1px solid #ffa500;
    border-radius: 5px;
    margin-right: 20px;
    padding: 0 10px;
    outline: none;
    height: 30px;
    line-height: 20px;
    font-size: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;

}

/* Table */

.table {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    color: #fff;
}

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
}

.row:hover {
    cursor: pointer;
}

.cell {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 5px;
    border: 1px solid #ffa500;
    background-color: #000;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.cell:first-child {
    width: 10%;
}

.cell:nth-child(2),
.cell:nth-child(3) {
    width: 15%;
}

.cell:nth-child(4) {
    width: 45%
}

.cell:last-child {
    width: 15%;
}

.cell > p {
    margin: 0;
    text-align: center;
    overflow: hidden;
}

.header-cell:hover,
.table-row:hover > .table-cell {
    background-color: #fff;
    color: #000;
}

.table-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.sort {
    margin-left: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: center;
        justify-content: center;
}

.sort > p {
    margin: 0;
    opacity: .5;
}

.sort > p.active {
    opacity: 1;
}

/* Pagination */

.pagination {
    height: 50px;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
    background-color: #fff;
    border: 1px solid #ffa500;
    font-weight: bold;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.pagination > span {
    margin-right: 10px;
}

.pagination > span:hover {
    cursor: pointer;
    text-decoration: underline;
    color: #ffa500;
}

.pagination > span.active {
    color: #ffa500;
}

/* User Info */

.user-info {
    text-align: left;
    border: 1px solid #ffa500;
    padding: 5px;
    background-color: #fff;
}

.user-info > p {
    margin: 5px 0;
}

.user-info > .description-header {
    margin-top: 20px;
}

.user-info > .description {
    margin-bottom: 20px;
}

@media screen and (max-width: 850px) {

    body {
        font-size: 14px;
    }
    
    .header-cell > .table-header {
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .sort {
        margin: 5px 0 0 0;
    }

    .cell:nth-child(4) {
        width: 40%
    }
    
    .cell:last-child {
        width: 20%;
    }
}

@media screen and (max-width: 576px) {

    body {
        font-size: 12px;
    }

    .search > .search__txt,
    .search > .btn {
        height: 20px;
        width: 150px;
        font-size: 16px;
        line-height: 16px;
    }

    .sort {
        margin: 2px 0 0 0;
    }
}

@media screen and (max-width: 576px) {

    .page-container {
        min-width: 230px;
    }
    .email {
        display: none;
    }

    .row > .cell {
        width: 35%;
    }

    .cell:first-child {
        width: 15%;
    }

    .cell:nth-child(2),
    .cell:nth-child(3) {
        width: 25%;
    }
}


